import React from "react";
import Logo from "../../assets/home/logo.png";
import styles from "./style.module.css";
import { WrapperContainer } from "../../style-app";
import { useState } from "react";
import HeaderHamburger from "./hamburger";

function Header() {
  // states
  const [open, setOpen] = useState(false);

  //hamburger actions
  const HandelOpen = () => setOpen(true);

  const HandelClose = () => setOpen(false);
  return (
    <>
      <div className={styles.section_header_home}>
        <WrapperContainer>
          <div className={styles.wrapper_header}>
            <img src={Logo} alt="photo" />
            <ul>
              <li className={styles.header_ul_li}>
                <a href="#">About Us</a>
              </li>
              <li className={styles.header_ul_li}>
                <a href="#">Leads</a>
              </li>
              <li className={styles.header_ul_li}>
                <a href="#">Contact Us</a>
              </li>
              <li className={styles.header_ul_li}>
                <div className={styles.header_phonenumber_box}>
                  <ion-icon name="phone-portrait-outline"></ion-icon>
                  <div className={styles.numbers}>
                    <a href="tel: 8 (988) 356 22 11">8 (988) 356 22 11</a>
                    <a href="tel: 8 (988) 356 22 11">8 (988) 356 22 11</a>
                  </div>
                </div>
              </li>
              <li className={styles.header_open_menu} onClick={HandelOpen}>
                <ion-icon name="menu-outline"></ion-icon>
              </li>
              <li className={styles.header_btn_box}>
                <button
                  className={`${styles.header_btn} ${styles.header_makecall_btn}`}
                >
                  Make a Call
                </button>

                <button
                  className={`${styles.header_btn} ${styles.header_emailus_btn}`}
                >
                  Email Us
                </button>
              </li>
            </ul>
          </div>
        </WrapperContainer>
      </div>
      <HeaderHamburger onClose={HandelClose} state={open} />
    </>
  );
}

export default Header;
