import React from "react";
import { WrapperContainer } from "../../../style-app";
import styles from "./style.module.css";

function HomeForBusiness() {
  return (
    <>
      <WrapperContainer>
        <div className={styles.wrapper_business}>
          <h2>FOR BUSINESS</h2>
          <div className={styles.bastet_business}>
            <h3>Bastet</h3>
            <div>
              <p>
                The most profitable purchases are made when we are not tied to a
                specific car, but simply play a profitable lot.
              </p>
              <p>
                Sometimes it is more profitable to buy a car on the way than to
                order it from the mainland.
              </p>
            </div>
          </div>
          <div className={styles.car_business}>
            <h3>Car</h3>
            <div>
              <p>
                We attract investors so that our customers have the opportunity
                to profitably and in the shortest possible time to purchase a
                car that is already heading here.
              </p>
              <button>Contact Us</button>
            </div>
          </div>
        </div>
      </WrapperContainer>
    </>
  );
}

export default HomeForBusiness;
