import React from "react";
import { WrapperContainer } from "../../../style-app";
import GetStarted from "../../../assets/home/home-get-started.png";
import styles from "./style.module.css";

function HomeGetStarted() {
  return (
    <>
      <div className={styles.section_get_started}>
        <WrapperContainer>
          <div className={styles.wrapper_get_started}>
            <div className={styles.head_get_started}>
              <h3>How To Get Started</h3>
              <p>
                Start receiving auto transport leads today. Fill out the form to
                start the purchase or contact us. We will send you a credit card
                authorization to process the first payment on your lead package.
                Once the payment is billed, leads are turned on immediately.
                Credit card payment will be charged the same day, every week. No
                contract is involved so you can stop your auto transport leads
                anytime. Recommended Auto Transport Brokers Software Jtracker:
                Work less and make more with jTracker Auto Transport Software!
                Efficiently manage your leads, quotes, orders, payments,
                dispatching and more, all from a single web-based program built
                just for auto transport brokers. Central Dispatch: Connect with
                over 12,000 professional auto transportation companies eager to
                ship your cars, trucks, and other vehicles. Leverage Central
                Dispatch’s powerful network of professional auto transportation
                companies to get your customer’s vehicles moved quickly and
                efficiently. Cronetic: Cronetic is a cloud-based Auto Transport
                Software for Auto Transport Brokers and Carriers which also
                includes portal for shippers. Easy and clean interface to
                optimize your auto transport brokerage business. BATS: Is the
                first complete CRM solution for auto transport brokers. BATS is
                a great option you need to automate, streamline, manage,
                operate, and squeeze your auto transport leads. Spam Leads
                Policy All of our leads are human-generated, and sometimes you
                will get a spam lead delivered. Because of that, we usually
                deliver a few extra leads on your weekly package to make up.
              </p>
            </div>
            <div className={styles.home_contact}>
              <img src={GetStarted} alt="photo" />
              <div className={styles.contact_us_box}>
                <h3>Contact US</h3>
                <p>A specialist will contact you within 15 minutes</p>
                <form>
                  <input type="text" placeholder="Company name" />
                  <input type="text" placeholder="Contact name" />
                  <input type="text" placeholder="Phone number" />
                  <input type="text" placeholder="Email address" />
                  <textarea placeholder="Inquiry"></textarea>
                  <button>Send Inquiry</button>
                </form>
              </div>
            </div>
          </div>
        </WrapperContainer>
      </div>
    </>
  );
}

export default HomeGetStarted;
