import React from "react";
import styles from "./styles.module.css";
import "./style.css";
import { Drawer, Space } from "antd";

function HeaderHamburger({ onClose, state }) {
  return (
    <>
      <Drawer
        title="Bastet Car"
        placement="left"
        onClose={onClose}
        open={state}
        width="100%"
        extra={
          <Space>
            <button className="drawers_close_btn" onClick={onClose}>
              <ion-icon name="close-outline"></ion-icon>
            </button>
          </Space>
        }
      >
        <div className={styles.header_hamburger_wrapper}>
          <ul>
            <li>
              <a href="#">About Us</a>
            </li>
            <li>
              <a href="#">Leads</a>
            </li>
            <li>
              <a href="#">Contact Us</a>
            </li>
            <li>
              <div className={styles.header_phonenumber_box}>
                <ion-icon name="phone-portrait-outline"></ion-icon>
                <div className={styles.numbers}>
                  <a href="tel: 8 (988) 356 22 11">8 (988) 356 22 11</a>
                  <a href="tel: 8 (988) 356 22 11">8 (988) 356 22 11</a>
                </div>
              </div>
            </li>
          </ul>
        </div>
      </Drawer>
    </>
  );
}

export default HeaderHamburger;
