import React from "react";
import { WrapperContainer } from "../../../style-app";
import styles from "./style.module.css";
import { Checkbox, Collapse } from "antd";
import "./style.css";

function HomeHero() {
  const text = `
  «Bastet car»  is an organization that supplies cars throughout the United States. It has become a favorite of many customers due to its quality and fast performance..
`;

  const items = [
    {
      key: "1",
      label: "Full cycle of services without intermediaries:",
      children: <p>{text}</p>,
    },
    {
      key: "2",
      label: "Full cycle of services without intermediaries:",
      children: <p>{text}</p>,
    },
    {
      key: "3",
      label: "Full cycle of services without intermediaries:",
      children: <p>{text}</p>,
    },
  ];

  return (
    <>
      <div className={styles.section_home_hero}>
        <WrapperContainer>
          <div className={styles.wrapper_home_hero}>
            <div className="hero_accordion">
              <Collapse
                defaultActiveKey={1}
                expandIcon={({ isActive }) => (
                  <>
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="6"
                      height="6"
                      viewBox="0 0 6 6"
                      fill="none"
                    >
                      <circle cx="2.75" cy="2.75" r="2.75" fill="white" />
                    </svg>
                  </>
                )}
                accordion
                items={items}
              />
              <button>Get Offers</button>
            </div>
            <div className={styles.hero_request_box}>
              <h3 className={styles.request_head_text}>ORDER LEADS</h3>

              <select>
                <option>125 LEAD (Up to 4 brokers)</option>
              </select>

              <h3>DELIVER TO</h3>

              <Checkbox>Checkbox</Checkbox>
              <Checkbox>Checkbox</Checkbox>
              <Checkbox>Checkbox</Checkbox>
              <Checkbox>Checkbox</Checkbox>
              <Checkbox>Checkbox</Checkbox>

              <h3>COMPANY NAME</h3>

              <input type="text" />

              <button>Request Leads Now</button>
            </div>
          </div>
        </WrapperContainer>
      </div>
      <div className={styles.hero_bottom_back}></div>
    </>
  );
}

export default HomeHero;
